"use client";

import { useState, useEffect } from "react";

export default function ProfilePage() {
  const [isRegister, setIsRegister] = useState(false);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [experienceLevel, setExperienceLevel] = useState("");
  const [message, setMessage] = useState("");
  const [user, setUser] = useState(null);

  // Check logged-in user on mount
  useEffect(() => {
    fetch("/api/auth/me")
      .then((res) => res.json())
      .then((data) => {
        if (data.user) setUser(data.user);
      });
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();
    const endpoint = isRegister ? "/api/auth/register" : "/api/auth/login";
    const bodyData = isRegister
      ? { email, password, experienceLevel }
      : { email, password };

    const res = await fetch(endpoint, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(bodyData),
    });

    const data = await res.json();
    if (res.ok) {
      setMessage(isRegister ? "Registration successful!" : "Login successful!");
      setUser(data.user);
      setEmail("");
      setPassword("");
      setExperienceLevel("");
    } else {
      setMessage(data.error || "Operation failed");
    }
  };

  const handleLogout = async () => {
    await fetch("/api/auth/logout", { method: "POST" });
    setUser(null);
    setMessage("Logged out successfully");
  };

  if (user) {
    return (
      <div className="profile-page">
        <h2>Your Profile</h2>
        <p>
          <strong>Email:</strong> {user.email}
        </p>
        <p>
          <strong>Experience Level:</strong> {user.experienceLevel}
        </p>
        <p>
          <strong>User ID:</strong> {user.id}
        </p>
        <button onClick={handleLogout}>Logout</button>
        {message && <p>{message}</p>}
      </div>
    );
  }

  return (
    <div className="profile-page">
      <h2>Profile</h2>

      {/* Toggle Login/Register */}
      <div className="toggle-buttons">
        <button onClick={() => setIsRegister(false)} disabled={!isRegister}>
          Login
        </button>
        <button onClick={() => setIsRegister(true)} disabled={isRegister}>
          Register
        </button>
      </div>

      {/* Form */}
      <form onSubmit={handleSubmit}>
        <h3>{isRegister ? "Register" : "Login"}</h3>

        <label>Email:</label>
        <input type="email" value={email} onChange={(e) => setEmail(e.target.value)} required />

        <label>Password:</label>
        <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} required />

        {isRegister && (
          <>
            <label>Are you new to sustainability?</label>
            <select value={experienceLevel} onChange={(e) => setExperienceLevel(e.target.value)} required>
              <option value="">Select</option>
              <option value="beginner">Yes, Iâ€™m new</option>
              <option value="experienced">No, I have experience</option>
            </select>
          </>
        )}

        <button type="submit">{isRegister ? "Register" : "Login"}</button>
      </form>
      {message && <p>{message}</p>}
    </div>
  );
}
